/*** Fonts ----------***/
@mixin googleFont($family, $weight: 400, $style: normal) {
		font-family: $family;
		font-weight: $weight;
		font-style: $style;
}

@mixin fontFace($name, $weight: normal) {
	@font-face {
		font-family: $name;
		src: url('../fonts/#{$name}/#{$name}.eot');
		src: url('../fonts/#{$name}/#{$name}.eot?#iefix') format('embedded-opentype'),
			url('../fonts/#{$name}/#{$name}.woff') format('woff'),
			url('../fonts/#{$name}/#{$name}.woff2') format('woff2'),
			url('../fonts/#{$name}/#{$name}.ttf') format('truetype'),
			url('../fonts/#{$name}/#{$name}.svg##{$name}') format('svg');
		font-weight: $weight;
	}
}

/*** Linear gradients ----------***/
@mixin linearGradientTop($top, $bottom){
  background: linear-gradient(to top,  $top 0%, $bottom 100%);
}
@mixin linearGradientBottom($top, $bottom){
  background: linear-gradient(to bottom,  $top 0%, $bottom 100%);
}
@mixin linearGradientLeft($top, $bottom){
  background: linear-gradient(to left,  $top 0%, $bottom 100%);
}
@mixin linearGradientRight($top, $bottom){
  background: linear-gradient(to right,  $top 0%, $bottom 100%);
}



/*** Buttons state ----------***/
@mixin button-bg($bg) {
  background-color: $bg;
	border-color: $bg;
	&:disabled, &.disabled {
		background-color: darken($bg,6%);
    border-color: darken($bg,6%);
	}
  &:not(:disabled):not(.disabled):hover {
    background-color: darken($bg,10%);
    border-color: darken($bg,10%);
    transition: all 0.3s ease;
  }
  &:not(:disabled):not(.disabled):active {
    background-color:darken($bg,18%);
    border-color:darken($bg,18%);
  }
  &:not(:disabled):not(.disabled).active {
    background-color:darken($bg,18%);
    border-color:darken($bg,18%);
  }
}
