
.btn {
  // font-weight: 400;
  border-width: 2px;
}

@each $btn, $bg in (
primary: $primary,
secondary: $secondary,
success: $success,
danger: $danger,
warning: $warning,
info: $info,
light: $light,
dark: $dark)
{
  .btn-#{$btn} {
    @include button-bg($bg);

    &.square {
  		border-radius: 0;
  	}

  	&.oval {
  		border-radius: 25px;
  		padding-left: 1rem;
  		padding-right: 1rem;
  	}
  }

  .btn-outline-#{$btn} {
    @include button-bg($bg);
    color: $bg;
    background-color: transparent;

    &:not(:disabled):not(.disabled):hover {
      background-color: $bg;
      border-color: $bg;
      color: $white;
    }

    &:disabled, &.disabled {
      background-color: transparent;
      color: $bg;
    }

    &.square {
  		border-radius: 0;
  	}

  	&.oval {
  		border-radius: 25px;
  		padding-left: 1rem;
  		padding-right: 1rem;
  	}
  }
}
.btn-icon {
  width: 40px;
  height: 40px;
  line-height: 36px;
  padding: 0;
  text-align: center;

  &.btn-sm {
    width: 35px;
    height: 35px;
    line-height: 31px;
  }

  &.btn-lg {
    width: 50px;
    height: 50px;
    line-height: 46px;
  }

  &.oval {
    width: 40px;
    height: 40px;
    line-height: 36px;
    border-radius: 50%;
    padding: 0;

    &.btn-sm {
      width: 35px;
      height: 35px;
      line-height: 31px;
    }

    &.btn-lg {
      width: 50px;
      height: 50px;
      line-height: 46px;
    }

  }
  i {
    margin: 0;
  }
  &-left i {
    margin-right: .75em;
  }
  &-right i {
    margin-left: .75em;
  }
  &-left.btn-sm i {
    margin-right: .5em;
  }
  &-right.btn-sm i {
    margin-left: .5em;
  }

}
