.form-group {
    label {
        font-size: 1rem;
    }
    .form-text {
        font-size: 90%;
        color: $light;
    }

    &.error {
        .form-control {
            border-color: $red;
            color: $red;
        }
        .form-text {
            color: $red;
        }
        ::-webkit-input-placeholder {
            color: $red;
          }
          ::-moz-placeholder {
            color: $red;
          }
          :-ms-input-placeholder {
            color: $red;
          }
          :-moz-placeholder {
            color: $red;
          }
    }

}
.form-control {
    height: 46px;
    line-height: 50px;
    background-color: transparent;
    border-width: 2px;
    border-color: $gray-200;
    color: $dark;

    &:focus {
        background-color: transparent;
        outline: 0;
        box-shadow: none;
        border-color: $blue;
    }

    &.square {
      border-radius: 0;
    }

    &.oval {
      border-radius: 25px;
      padding: .375rem 1.25rem;
    }

    &-lg {
        height: 56px;
        line-height: 60px;

        &.oval {
          padding: .5rem 1.35rem;
        }
    }

    &-sm {
        height: 36px;
        line-height: 40px;
        &.oval {
          padding: .25rem .95rem;
        }
    }



}

.form-control:disabled, .form-control[readonly] {
    background-color: transparent;
    opacity: 0.5;
    &:focus, &:hover {
        background-color: transparent;
        border-color: $gray-200;
        opacity: 0.5;
    }
}

.input-group {
  .input-group-append + .form-control {
    order: -1;
    border-radius: 0.25em 0 0 0.25em;
  }
  &.square {
    .input-group-text {
      border-radius: 0;
    }
    .form-control {
      border-radius: 0;
    }
  }
  &.oval {
    .input-group-prepend {
      .input-group-text {
        border-radius: 25px 0 0 25px;
      }
      & + .form-control {
        border-radius: 0 25px 25px 0;
        padding-right: 1.25rem;
      }
    }
    .input-group-append {
      .input-group-text {
        border-radius: 0 25px 25px 0;
      }
      & + .form-control {
        border-radius: 25px 0 0 25px;
        padding-left: 1.25rem;
      }
    }

  }
  &-lg {
    .input-group-text {
      height: 56px !important;
      padding: .375rem 1.25rem !important;
    }
    &.oval {
      .form-control {
        padding-left: 1.35rem;
        padding-right: 1.35rem;
      }
    }
  }
  &-sm {
    .input-group-text {
      height: 36px !important;
      padding: .25rem .75rem !important;
    }
    &.oval {
      .form-control {
        padding-left: .95rem !important;
        padding-right: .95rem !important;
      }
    }
  }
  .input-group-text {
    padding: .375rem 1rem;
    border: 2px solid $gray-200;
    background: $gray-200;
    color: $dark;
  }
  .input-group-prepend {
    .input-group-text {
      border-right: 0;
    }
  }
  .input-group-append {
    .input-group-text {
      border-left: 0;
    }
  }
}

.input-group-lg {
  .form-control-lg {
      height: 56px;
      line-height: 60px;
  }
}

.input-group-sm {
  .form-control-sm {
    height: 36px;
    line-height: 40px;
  }
}


.select {
  padding: 0;

  &.form-control {
    border: 0;
  }
  .ui-selectmenu-menu .ui-widget-content {
    border-radius: 0 0 .2rem .2rem;
    .ui-menu-item {
      padding: .5rem 1rem;
    }
  }
  &.square {
    .ui-selectmenu-button {
      border-radius: 0;
    }


    .ui-selectmenu-menu .ui-widget-content {
      border-radius: 0;
    }
  }
  &.oval {
    padding: 0;

    .ui-selectmenu-button {
      border-radius: 25px;
      &-open {
        border-radius: 25px 25px 0 0;
      }
    }
    .ui-selectmenu-menu .ui-widget-content {
      border-radius: 0 0 25px 25px;
    }
  }

  .ui-selectmenu-button {
    padding: .65rem .85rem .375rem;
    width: calc(100% - 1.7rem);
    border-radius: .2rem;
    &-open {
      border-radius: .2rem .2rem 0 0;
    }
  }
  .ui-selectmenu-text {
    font-size: 1rem;
  }
}
.form-control-lg {
  .ui-selectmenu-button {
    padding: .65rem 1rem .5rem;
    width: calc(100% - 2rem);
  }
  .ui-selectmenu-text {
    font-size: 1.25rem;
  }
  .ui-icon {
    margin-top: 5px;
  }
  .ui-selectmenu-menu  .ui-widget-content {
    .ui-menu-item {
      padding: .5rem 1.2rem;
    }
  }

}
.form-control-sm {
  .ui-selectmenu-button {
    padding: .5rem .75rem .15rem;
    width: calc(100% - 1.5rem);

  }
  .ui-selectmenu-text {
    font-size: .875rem;
  }
  .ui-icon {
    margin-top: -2px;
  }
  .ui-selectmenu-menu  .ui-widget-content {
    .ui-menu-item {
      padding: .5rem 1rem;
    }
  }
  &.oval {
    .ui-selectmenu-button {
      &-open {
        border-radius: 20px 20px 0 0 !important;
      }
    }
  }
}

.tags-autocomplete {
  .ui-widget-content {
    background: transparent;
    border-color: transparent;

    .tag-inner {
      display: inline-block;
      padding: 6px 36px 6px 12px;
      margin: 0 5px 5px 0;
      border: 2px solid;
      border-color: #e9eff3;
      border-radius: 0.25rem;
      color: #484b58;
      position: relative;
      .close {
        position: absolute;
        font-size: 1.2rem;
        right: 12px;
        top: 6px;
        color: #484b58;
        text-shadow: 0 1px 0 #ffffff;
      }
    }
  }
  .tags-options {
    border: 3px solid #f0f;
    background: #fcf;
    margin-top: 10px;
  }
}


.bg_black {
  .form-control {
    color: $white;
  }
  .changed .ui-selectmenu-text {
    color: $white;
  }
  .ui-selectmenu-menu {
    .ui-widget-content {
      background: $black;
      .ui-menu-item {
        &:hover {
          background: $gray_900;
        }
      }
      .ui-menu-item-wrapper {
        color: $white;

      }
    }
  }
  .tags-autocomplete {
    .ui-widget-content {
      .tag-inner {
        color: #e9eff3;
        .close {
          color: #e9eff3;
          text-shadow: 0 1px 0 #2d2f33;
        }
      }
    }
  }
}
