// Gradient

@mixin linear-gradient($args...) {
 background: linear-gradient($args);
}

$colors: (
  blue: $blue,
  indigo: $indigo,
  purple: $purple,
  pink: $pink,
  red: $red,
  orange: $orange,
  yellow: $yellow,
  green: $green,
  teal: $teal,
  cyan: $cyan,
  black: $black,
  dark: $dark,
  gray: $gray,
  light: $light,
  white: $white
);

@each $name, $color in $colors {

  @for $i from 1 through length($colors) {

    $name_1: nth(map-keys($colors), $i);
    $color_1: color($name_1);

    .gradient_horizontal_#{$name}-#{$name_1} {
      @include linear-gradient(to left, $color, $color_1);

      &:not(:disabled):not(.disabled):hover {
        @include linear-gradient(to left, darken($color, 5%), darken($color_1, 5%));
        transition: all 0.3s ease;
      }

      .outline {
        span {
          @include linear-gradient(to left, $color, $color_1);
          -webkit-background-clip: text!important;
          -webkit-text-fill-color: transparent!important;
        }
      }
    }

    .gradient_vertical_#{$name}-#{$name_1} {
      @include linear-gradient(to top, $color, $color_1);

      &:not(:disabled):not(.disabled):hover {
        @include linear-gradient(to top, darken($color, 5%), darken($color_1, 5%));
        transition: all 0.3s ease;
      }
      .outline {
        span {
          @include linear-gradient(to top, $color, $color_1);
          -webkit-background-clip: text!important;
          -webkit-text-fill-color: transparent!important;
        }
      }
    }

    .gradient_degree_#{$name}-#{$name_1} {
      @include linear-gradient(-15deg, $color, $color_1);

      &:not(:disabled):not(.disabled):hover {
        @include linear-gradient(-15deg, darken($color, 5%), darken($color_1, 5%));
        transition: all 0.3s ease;
      }
      .outline {
        span {
          @include linear-gradient(-15deg, $color, $color_1);
          -webkit-background-clip: text!important;
          -webkit-text-fill-color: transparent!important;
        }
      }
    }


    .dropcap {
      &.gradient_horizontal_#{$name}-#{$name_1} {
        background: transparent!important;
        >span {
          @include linear-gradient(to left, $color, $color_1);
        }
        &.outline {
          span {
            background: linear-gradient(to left, $color 40%, $color_1 60%);
            color: $color;

            &:before {
              background: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" preserveAspectRatio="none"><defs><linearGradient id="gradient" x1="0%" y1="0%" x2="100%" y2="0%"><stop offset="0%" stop-color="#{$color_1}" stop-opacity="0.9" /><stop offset="100%" stop-color="#{$color}" stop-opacity="1.0" /></linearGradient></defs><rect x="2" y="2" rx="4" ry="4" style="fill:none;stroke:url(#gradient);stroke-width:2;width: 70px; height: 70px" /></svg>');
            }
          }

          &.square {
            span {
              &:before {
                background: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" preserveAspectRatio="none"><defs><linearGradient id="gradient" x1="0%" y1="0%" x2="100%" y2="0%"><stop offset="0%" stop-color="#{$color_1}" stop-opacity="0.9" /><stop offset="100%" stop-color="#{$color}" stop-opacity="1.0" /></linearGradient></defs><rect x="2" y="2" rx="0" ry="0" style="fill:none;stroke:url(#gradient);stroke-width:2;width: 70px; height: 70px" /></svg>');
              }
            }
          }
          &.oval {
            span {
              &:before {
                background: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" preserveAspectRatio="none"><defs><linearGradient id="gradient" x1="0%" y1="0%" x2="100%" y2="0%"><stop offset="0%" stop-color="#{$color_1}" stop-opacity="0.9" /><stop offset="100%" stop-color="#{$color}" stop-opacity="1.0" /></linearGradient></defs><rect x="2" y="2" rx="50" ry="50" style="fill:none;stroke:url(#gradient);stroke-width:2;width: 70px; height: 70px" /></svg>');
              }
            }
          }

        }

      }

      &.gradient_vertical_#{$name}-#{$name_1} {
        background: transparent!important;
        >span {
          @include linear-gradient(to top, $color, $color_1);
        }
        &.outline {
          span {
            background: linear-gradient(to top, $color 40%, $color_1 60%);
            color: $color;

            &:before {
              transform: rotate(90deg);
              background: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" preserveAspectRatio="none"><defs><linearGradient id="gradient" x1="0%" y1="0%" x2="100%" y2="0%"><stop offset="0%" stop-color="#{$color_1}" stop-opacity="0.9" /><stop offset="100%" stop-color="#{$color}" stop-opacity="1.0" /></linearGradient></defs><rect x="2" y="2" rx="4" ry="4" style="fill:none;stroke:url(#gradient);stroke-width:2;width: 70px; height: 70px" /></svg>');
            }
          }

          &.square {
            span {
              &:before {
                background: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" preserveAspectRatio="none"><defs><linearGradient id="gradient" x1="0%" y1="0%" x2="100%" y2="0%"><stop offset="0%" stop-color="#{$color_1}" stop-opacity="0.9" /><stop offset="100%" stop-color="#{$color}" stop-opacity="1.0" /></linearGradient></defs><rect x="2" y="2" rx="0" ry="0" style="fill:none;stroke:url(#gradient);stroke-width:2;width: 70px; height: 70px" /></svg>');
              }
            }
          }
          &.oval {
            span {
              &:before {
                background: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" preserveAspectRatio="none"><defs><linearGradient id="gradient" x1="0%" y1="0%" x2="100%" y2="0%"><stop offset="0%" stop-color="#{$color_1}" stop-opacity="0.9" /><stop offset="100%" stop-color="#{$color}" stop-opacity="1.0" /></linearGradient></defs><rect x="2" y="2" rx="50" ry="50" style="fill:none;stroke:url(#gradient);stroke-width:2;width: 70px; height: 70px" /></svg>');
              }
            }
          }
        }
      }

      &.gradient_degree_#{$name}-#{$name_1} {
        background: transparent!important;
        >span {
          @include linear-gradient(217deg, $color, $color_1);
        }
        &.outline {
          span {
            background: linear-gradient(217deg, $color 40%, $color_1 60%);
            color: $color;
            &:before {
              background: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" preserveAspectRatio="none"><defs><linearGradient id="gradient" gradientTransform="rotate(-45)" x1="0%" y1="0%" x2="100%" y2="0%"><stop offset="0%" stop-color="#{$color_1}" stop-opacity="0.9" /><stop offset="100%" stop-color="#{$color}" stop-opacity="1.0" /></linearGradient></defs><rect x="2" y="2" rx="4" ry="4" style="fill:none;stroke:url(#gradient);stroke-width:2;width: 70px; height: 70px" /></svg>');
            }
          }
          &.square {
            span {
              &:before {
                background: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" preserveAspectRatio="none"><defs><linearGradient id="gradient" gradientTransform="rotate(-45)" x1="0%" y1="0%" x2="100%" y2="0%"><stop offset="0%" stop-color="#{$color_1}" stop-opacity="0.9" /><stop offset="100%" stop-color="#{$color}" stop-opacity="1.0" /></linearGradient></defs><rect x="2" y="2" rx="0" ry="0" style="fill:none;stroke:url(#gradient);stroke-width:2;width: 70px; height: 70px" /></svg>');
              }
            }
          }
          &.oval {
            span {
              &:before {
                background: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" preserveAspectRatio="none"><defs><linearGradient id="gradient" gradientTransform="rotate(-45)" x1="0%" y1="0%" x2="100%" y2="0%"><stop offset="0%" stop-color="#{$color_1}" stop-opacity="0.9" /><stop offset="100%" stop-color="#{$color}" stop-opacity="1.0" /></linearGradient></defs><rect x="2" y="2" rx="50" ry="50" style="fill:none;stroke:url(#gradient);stroke-width:2;width: 70px; height: 70px" /></svg>');
              }
            }
          }
        }
      }

    }
    .dropcap[class*="gradient_"] {
      &.outline {
        span {
          display: inline-block;
          width: 74px;
          height: 74px;
          position: relative;
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent;
          display: table;

          &:before {
            content: '';
            display: inline-block;
            width: 100%;
            height: 100%;

            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
          }
        }

      }
    }

    .btn[class*="gr-"] {
      border-width: 0;
      padding: 0;
      position: relative;
      z-index: 2;
      padding: 2px;

      span {
        display: block;
        padding: .375rem .75rem;
        border-radius: .25rem;
        color: $white;
      }

      &.square {
        border-radius: 0;
      }

      &.oval {
        border-radius: 25px;

        .outline {
          border-radius: 25px;

        }


        span {
          padding-left: 1rem;
          padding-right: 1rem;
        }
      }

      &:not(:disabled):not(.disabled){
        .outline:hover {
          background-color: transparent;
          span {
            -webkit-text-fill-color: $white!important;
          }
        }
      }
    }
  }
}
