body {
  // background-color: $light;
  color: $black;
}
.container-fluid {
  max-width: 100%;
}
header {
  padding: 30px 0;
}
.demo header h2 {
  margin-bottom: 0;
}
.demo_title {
  margin: 15px 0;
}
.separator {
  width: 100%;
  height: 108px;
}
.bg_height {
  min-height: 100vh;
  height: 100%;
}
.bg_black {
  color: $white;
  a, a:hover, a:focus {
    color: $yellow;
  }
}

.colors {
  .item-inner  {
    margin-left: -15px;
    margin-right: -15px;
  }
  .item {
    display: inline-block;
    width: 145px;
    height: 145px;
    text-align: center;
    font-size: 12px;
    color: $white;
    font-weight: 400;
    border-radius: 50%;
    padding-top: 54px;
    margin: 15px;
    box-shadow: 3px 3px 33px rgba(0,0,0,0.25);
    text-transform: uppercase;

    span {
      display: block;
    }

  }
}
.gradients {
  .item {
    margin: 15px;
    padding: 30px 0;
    text-align: center;
    color: $white;
    text-transform: uppercase;

    span {
      display: block;
    }
  }
}
.buttons {
  .btn {
    margin-right: 12px;
    margin-bottom: 12px;
  }
}
.button-group {
  .btn-group {
    margin-right: 12px;
    margin-bottom: 12px;
  }
}

.icons {
  .item-inner {
    overflow: hidden;
  }
  .item {
    float: left;
    width: 6.25%;
    margin-bottom: 15px;
    i {
      font-size: 18px;
    }
  }
}
.demo-navbar {
  .btn {
    color: $red!important;
    border-width: 1px;
    padding: .25rem 1.25rem;
    margin-top: 5px;
    &:hover {
      color: $white!important;
    }
  }
}

.demo-card-items {
  height: 100px;

  a {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    text-transform: uppercase;
    text-decoration: none;
    color: $black;
  }

  &:hover {
    box-shadow: 0 0 8px rgba(0,0,0,0.2);
  }
}
