$colors: (
  blue: $blue,
  indigo: $indigo,
  purple: $purple,
  pink: $pink,
  red: $red,
  orange: $orange,
  yellow: $yellow,
  green: $green,
  teal: $teal,
  cyan: $cyan,
  black: $black,
  dark: $dark,
  gray: $gray,
  light: $light,
  white: $white,
  100: $gray-100,
  200: $gray-200,
  300: $gray-300,
  400: $gray-400,
  500: $gray-500,
  600: $gray-600,
  700: $gray-700,
  800: $gray-800,
  900: $gray-900,
);

@each $name, $color in $colors {
  /*** Colors ----------***/
  .color_#{$name} {
    color: $color;
  }
  /*** Backgrounds ----------***/
  .bg_#{$name} {
    background-color: $color;
  }

}
