h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
  @include googleFont($font_2, 400);
  line-height: 1.5;

  &.light {
    @include googleFont($font_2, 300);
  }
  &.bold {
    @include googleFont($font_2, 700);
  }
  &.uppercase {
    text-transform: uppercase;
  }
}
.display-1, .display-2, .display-3, .display-4 {
  @include googleFont($font_2, 400);
}

p {
  font-size: 14px;
}

a, a:hover, a:focus {
  color: $primary;
}

@each $text, $color in (
primary: $primary,
muted: $secondary,
secondary: $secondary,
success: $success,
danger: $danger,
warning: $warning,
info: $info,
blue: $blue,
indigo: $indigo,
purple: $purple,
pink: $pink,
red: $red,
orange: $orange,
yellow: $yellow,
green: $green,
teal: $teal,
cyan: $cyan,
black: $black,
dark: $dark,
gray: $gray,
light: $light,
white: $white,
gray-100: $gray-100,
gray-200: $gray-200,
gray-300: $gray-300,
gray-400: $gray-400,
gray-500: $gray-500,
gray-600: $gray-600,
gray-700: $gray-700,
gray-800: $gray-800,
gray-900: $gray-900,
)
{
  .text-#{$text} {
    color: #{$color} !important;
  }

  .dropcap {
    >span {
      line-height: 74px;
      width: 74px;
      height: 74px;
      border-radius: 4px;
      color: #fff;
      background: $color;
      display: inline-block;
      float: left;
      font-size: 50px;
      margin: 5px 15px 3px 0;
      text-align: center;
      text-transform: uppercase;
    }
    &.square  {
      >span {
        border-radius: 0;
      }
    }

    &.oval {
      >span {
        border-radius: 50%;
      }
    }
    &.#{$text} {
      >span {
        background-color: #{$color};
        border: 2px solid #{$color};
        box-sizing: border-box;
      }


      &.outline {
        >span {
          background-color: transparent !important;
          color: #{$color} !important;
        }
      }


    }


    &[class^="gr-"] {

    }

  }
}

.gradient {
  span {
    display: inline-block;
    width: 74px;
    height: 74px;
    position: relative;
    background: linear-gradient(90deg, #0070d8 40%, #83eb8a 60%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    color: #0070d8;
    display: table;

    &:before {
      content: '';
      display: inline-block;
      width: 100%;
      height: 100%;
      background: url(../../img/border/radius.svg);
      background-size: 100% 100%;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
    }
  }

}
