@each $btn, $bg in (
primary: $primary,
secondary: $secondary,
success: $success,
danger: $danger,
warning: $warning,
info: $info,
light: $light,
dark: $dark)
{
  .labeled {

    .btn-#{$btn} {

      &+.btn {
        margin-left: 0;
        border-left-color: transparent!important;
      }
      &:first-child {

        border-right-color: $bg!important;

      }
      &.btn-icon {
        @include button-bg($bg);
        background-color: darken($bg,5%);
        border-color: darken($bg,5%);
      }
    }
    &:hover {
        .btn-#{$btn} {
          background-color: darken($bg,10%);
          border-color: darken($bg,10%);
          transition: all 0.3s ease;
      }
    }


    .btn-outline-#{$btn} {

      &+.btn {
        margin-left: 0;
        border-left-color: transparent!important;

      }
      &:first-child {

        border-right-color: darken($bg,5%)!important;

      }
    }
    &:hover {
        .btn-outline-#{$btn} {
          background-color: $bg;
          border-color: $bg;
          color: $white;
        }
    }
  }
}
